default:root {
  --test: calc(91.75vh - 20px);
}

@keyframes swipe-in {
  0% {
    left: 160%;
  }

  100% {
    left: 0%;
  }
}

@keyframes swipe-out {
  0% {
    left: 0%;
  }

  100% {
    left: 160%;
  }
}

body {
  text-align: center;
  margin: 0;
  font-size: medium;
  font-weight: bold;
}

.default, .Counter, .Totals, #Notification, #Exit {
  color: #fff;
  background-color: #6079ab;
  border: 2px solid #000;
  border-radius: 10px;
  height: 40px;
  font-weight: bold;
}

#Exit {
  width: 100px;
  height: 50px;
  position: absolute;
  top: 3vh;
  left: 50px;
}

.default {
  width: 125px;
  font-size: small;
}

.Counter {
  text-align: center;
  width: 50px;
  font-size: medium;
}

.Totals {
  text-align: center;
  width: 125px;
  font-size: small;
  line-height: 20px;
}

#Notification, #Empty {
  justify-content: center;
  align-items: center;
  width: 125px;
  font-size: small;
  display: flex;
}

.notif-in {
  animation-name: swipe-in;
  animation-duration: 2.5s;
  animation-timing-function: ease;
  position: relative;
}

.notif-out {
  animation-name: swipe-out;
  animation-duration: 2.5s;
  animation-timing-function: ease;
  position: relative;
}

#Prod_Info_Table {
  border-collapse: collapse;
  width: 25vw;
  height: 60vh;
  position: absolute;
}

#Prod_Info_Table > tr {
  height: 10%;
}

#Prod_Info_Table > tr > td {
  border: 2px solid #000;
}

#Basket_Table {
  border-collapse: collapse;
  width: 80vw;
  position: absolute;
}

#Headers {
  height: 10vh;
}

#Basket_Table, #Basket_Table > tr, #Headers > th {
  border: 2px solid #000;
}

.Prod_IMG_Container, #Prod_IMG_Div, #Prod_IMG {
  height: 15vh;
}

#PopUp_Table {
  border-collapse: collapse;
  background-color: #ededed;
  width: 30vw;
}

#PopUp_Table > tr > td {
  border: 3px solid #000;
}

#Total_Price, #Total_Quant, #Diff_Quant, #Checkout_Button, #BFB {
  margin: 0;
  padding: 0;
  position: absolute;
  left: calc(90vw - 62.5px);
}

#Checkout_Button, #BFB, #fullscreen {
  width: 125px;
  height: 44px;
}

.blur {
  filter: blur(5px);
}

.NoBlur {
  filter: blur();
}

#fullscreen_div {
  z-index: 5;
  position: absolute;
  top: calc(50% - 25px);
  left: calc(50vw - 150px);
}

#Basket_Button {
  position: absolute;
  top: calc(91.75vh - 20px);
  left: calc(95vw - 62.5px);
}

#BTS_Button {
  position: absolute;
  top: calc(77.5vh - 20px);
  left: calc(95vw - 62.5px);
}

#Prod_Info_Table_Div {
  position: absolute;
  left: 66.5vw;
}

#BFB {
  top: 85vh;
}

#Total_Price {
  line-height: 40px;
  top: 10vh;
}

#Total_Quant {
  line-height: 40px;
  top: 25vh;
}

#Diff_Quant {
  top: 40vh;
}

#Notif_container {
  top: 10vh;
  left: 87.5vw;
}

#Checkout_Button {
  top: 70vh;
}

#remove {
  font-size: small;
}
